#contact1{

width:80%;
height:auto;
display: grid;
border:0px solid white;
grid-template-columns: repeat(3,1fr);
grid-template-rows: auto;
gap:20px;
margin: auto;



}


#contact1>div{
    border:0px solid white;
}


a{
    text-decoration: none;
    color:white;
}

#projects1{
    width:100%;
    height:auto;
    margin: auto;
    margin-top: 100px;
    margin-bottom: 100px;
}


#projects1>div{
    display: flex;
    width:90%;
    gap:20px;
    margin:auto;
    border: 0px solid white;
    margin-top: 150px;
    margin-bottom: 20px;
    

}

#projects1>div>div:nth-child(1){
    border: 0px solid white;
    width:50%;
    
}

#projects1>div>div:nth-child(2){
    border: 0px solid white;
    width:50%;
    background-color: rgb(3, 7, 35);;
    padding: 30px 0px 30px 0px;
    border-radius: 20px;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;

    
}



.ball {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    margin-bottom: 30px;

}

#react1{
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    padding: 10px;
    border-radius: 20px;
    background-color: aliceblue;
    transition: .2s;
}
  #react1:hover{
    transform: scale(1.5)
  }

