.h1{
    font-family:cursive
}


.code{
    padding: 20px 40px;
    border-radius: 20px;
    border: none;
    margin-right: 10px;
    font-family: 'Times New Roman', Times, serif;
    font-size: 15px;
    color: rgb(11, 11, 11);
    background: linear-gradient(to right, #33ccff 0%, #ff99cc 100%);

}
.code:hover{
    box-shadow: rgba(12, 135, 157, 0.35) 0px 5px 15px;
    border: 0.5px solid gray;
}

.img-code{
    margin-bottom: -30px;

}


.project-card>div:nth-child(1){
    transition: .2s;  
}
.project-card>div:nth-child(1):hover{
    background-color: aliceblue;
    margin-left: 100px;
    border-radius: 40px;
    transform: scale(1.5)
}
#contact1{
    color:black;
    text-transform: capitalize;

}
#contact1>div{
    height: 400px;
}
#contact1>div:hover{
    box-shadow: rgba(3, 3, 47, 0.2) 0px 7px 29px 0px;
    border:gray;
    height:400px;
    border-radius: 20px;
    border:1px solid rgb(12, 195, 227)
}
#contact-phone>a>h2{
    color: black;

}
#hello{
    color: black;
}

@media only screen and (max-width:800px) and (min-width:500px){

    #tech1{
       grid-template-columns: repeat(4,1fr);
       grid-template-rows: auto;
    }
    #projects1{
        margin: auto;
        width:120%;
        margin-left: 120px;
    }
    #projects1>div{
        display: grid;
        

    }
    #contact1{
        grid-template-columns: repeat(1,1fr);
        grid-template-rows: auto;
    }
    .project-card>div:nth-child(1):hover{
        transform: scale(1.2);
        margin-left: 0;
    }

}

#about1{
    height: 60px;
    width:100%;
}
#skills1{
    height: 60px;
    width: 100%;
}
#projects11{
    height: 60px;
    width: 100%;
}

@media only screen and (max-width:500px) and (min-width:0px){

    #tech1{
       grid-template-columns: repeat(3,1fr);
       grid-template-rows: auto;
